import Glide from "@glidejs/glide";

export default class GenericSlider {
    constructor(selector) {
        try {
            this.initObserver(selector)
        } catch (e) {
            document.body.addEventListener('intersection-observer-loaded', () => {
                this.initObserver(selector)
            })
        }

        document.body.addEventListener('product-recommendations-loaded', (e) => {
            this.initObserver(selector)
        })
    }

    initObserver(selector) {
        const observer = new IntersectionObserver(this.handleIntersection.bind(this), {
            threshold: 0
        });
        document.querySelectorAll(selector).forEach(el => {
            observer.observe(el);
        });
    }

    handleIntersection(entries) {
        entries.forEach(entry => {
            if (entry.isIntersecting) {
                if (entry.target.classList.contains('glide--carousel')) {
                    return
                }
                setTimeout(() => {
                    this.initSlider(entry.target);
                }, 1);
            }
        })
    }

    showArrowsWhenNeeded(arrows, glide) {
        if (glide.numSlides == null) return;

        if (!arrows) return;

        if (glide.numSlides <= glide.settings.perView) {
            arrows.style.display = 'none';
        } else {
            arrows.style.display = '';
        }
    }

    /*
    * Creates "fake" or "placeholder" slides at the beginning and end of carousels to fix empty space
    * caused by wrong transitioning of infinite carousels in glide.js
    * Issue can't be fixed directly in glide.js because of wrong design of carousel functionality.
    * */
    fixCarouselType(glide, el) {
        if (glide.settings.type === 'carousel') {
            const slides = el.querySelector('.glide__slides')
            let firstElToClone = slides.children[0]
            let lastElToClone = slides.children[slides.children.length - 1]
            if(el.dataset['numSlides'] > 2) {
                firstElToClone = slides.children[0].nextElementSibling.nextElementSibling
                lastElToClone = slides.children[slides.children.length - 1].previousElementSibling.previousElementSibling
            }
            const firstSlide = lastElToClone.cloneNode(true)
            const lastSlide = firstElToClone.cloneNode(true)
            firstSlide.classList.add('glide__slide-fake-first')
            firstSlide.style.right = `calc(100% + ${glide.settings.gap}px)`
            firstSlide.style.margin = 0
            lastSlide.classList.add('glide__slide-fake-last')
            lastSlide.style.left = `calc(100% + ${glide.settings.gap}px)`
            lastSlide.style.margin = 0
            slides.prepend(firstSlide)
            slides.append(lastSlide)
        }
    }

    initSlider(el) {
        if (!window.glide_options || !window.glide_options[el.dataset['genericSlider']]) {
            return
        }

        const glide = new Glide(el, window.glide_options[el.dataset['genericSlider']]);
        glide.numSlides = el.dataset['numSlides'] !== undefined ? parseInt(el.dataset['numSlides']) : null;

        /* do not initialize sliders with insufficient number of slides */
        if (glide.numSlides && glide.numSlides <= glide.settings.perView) {
            return
        }
        const arrows = glide.selector.querySelector('.glide__arrows')

        glide.on('resize', () => {
            this.showArrowsWhenNeeded(arrows, glide);
        });

        glide.on('mount.after', () => {
            this.showArrowsWhenNeeded(arrows, glide);
            this.fixCarouselType(glide, el);
        });

        glide.mount();

        window.glideInstances = window.glideInstances || {};
        window.glideInstances[el.dataset['genericSlider']] = glide;
        const event = new CustomEvent('glide-initialized', {detail: glide})
        document.body.dispatchEvent(event)

        glide.on('run', () => {
            document.querySelectorAll('.js-youtube').forEach((video) => {
                video.contentWindow.postMessage('{"event":"command","func":"' + 'pauseVideo' + '","args":""}', '*');
            });
        })
    }


    pauseAllMedia() {
        document.querySelectorAll('.js-youtube').forEach((video) => {
            video.contentWindow.postMessage('{"event":"command","func":"' + 'pauseVideo' + '","args":""}', '*');
        });
        document.querySelectorAll('.js-vimeo').forEach((video) => {
            video.contentWindow.postMessage('{"method":"pause"}', '*');
        });
        document.querySelectorAll('video').forEach((video) => video.pause());
        document.querySelectorAll('product-model').forEach((model) => {
            if (model.modelViewerUI) model.modelViewerUI.pause();
        });
    }
}