import debounce from 'lodash/debounce';
import '../header/js/main';
import '../generic_slider/js/main';
import '../gallery_modal/js/main';
import 'lazysizes';
import 'lazysizes/plugins/parent-fit/ls.parent-fit';

export default class Global {
    constructor() {
        this.initHeaders();
        this.noScrollClass = 'overflow-hidden';

        window.addEventListener("DOMContentLoaded", () => {

            document.addEventListener('click', event => {
                const el = event.target.closest('.js-switch-text');
                if (el) {
                    const target = el.dataset.switchTarget ? el.querySelector(el.dataset.switchTarget) : el
                    const state = el.dataset.switchState
                    if (state === "true") {
                        el.dataset.switchState = "false"
                        target.innerText = el.dataset.switchFalse
                        return
                    }
                    el.dataset.switchState = "true"
                    target.innerText = el.dataset.switchTrue
                }
            });

            const stickyHeader = document.querySelector('.js-sticky-header');
            if (typeof (stickyHeader) != 'undefined' && stickyHeader != null && window.location.pathname === "/") {
                this.initStickyHeader(stickyHeader);
            }

            this.initSidebars();
            this.initTooltips();
            this.initShowMoreButtons();

            if (window.matchMedia('(min-width: 1200px)').matches) {
                this.initHoverCollection();
            }

            window.addEventListener('resize', () => {
                if (window.innerWidth >= 1200 && window.matchMedia('(min-width: 1200px)').matches) {
                    this.initHoverCollection();
                }
            })

            AOS.init({
                once: true,
                duration: 600,
                disable: function () {
                    return window.innerWidth < '991';
                }
            });
        });
    }

    initHoverCollection() {
        const links = document.querySelectorAll('.cl-link');
        const images = document.querySelectorAll('.cl-image');

        if (links.length) {
            this.itemsRemoveClass(links);
            links[0].classList.add('active');

            links.forEach((imageTarget) => {
                imageTarget.addEventListener('mouseover', () => {
                    this.itemsRemoveClass(links);

                    imageTarget.classList.add('active');

                    images.forEach((el) => {
                        el.classList.remove('active');
                    })


                    const dataTarget = imageTarget.dataset.imageTarget;
                    document.querySelector('[data-image-id="' + dataTarget + '"]').classList.add('active');
                })
            });
        }

    }

    itemsRemoveClass(items) {
        items.forEach((el) => {
            el.classList.remove('active');
        })
    }

    initStickyHeader(header) {
        let isLight = false
        if (document.querySelector('.main-header').classList.contains('light')) {
            isLight = true;
        }
        window.onscroll = function () {
            if (window.scrollY > 50) {
                header.classList.add('bg-light');
                if (isLight) {
                    header.classList.remove('light');
                    header.classList.add('dark');
                }
            } else {
                header.classList.remove('bg-light');
                if (isLight) {
                    header.classList.add('light');
                    header.classList.remove('dark');
                }
            }
        };
    }

    initShowMoreButtons() {
        const buttons = document.querySelectorAll('[data-text-target]');
        if (buttons.length) {
            buttons.forEach((el) => {
                const clampClass = 'line-clamp-3';
                const btnShowText = el.dataset.textShow;
                const btnHideText = el.dataset.textHide;
                const targetId = el.dataset.textTarget;
                const target = document.querySelector('[data-text-id="' + targetId + '"]');

                el.addEventListener('click', () => {
                    el.classList.toggle('active');

                    if (target.classList.contains(clampClass)) {
                        target.classList.remove(clampClass);
                        el.querySelector('span').innerHTML = btnHideText;
                    } else {
                        target.classList.add(clampClass);
                        el.querySelector('span').innerHTML = btnShowText;
                    }
                })
            })
        }
    }

    initCollapseHorizontal() {
        const btn = document.querySelectorAll('.js-collapse-horizontal');
        btn.forEach(el => {
            const attr = el.getAttribute('data-target');
            const collapseEl = document.getElementById(attr);
            el.addEventListener('click', (e) => {
                collapseEl.classList.toggle('collapsed');
                el.classList.contains("collapsed") ? el.classList.remove('collapsed') : el.classList.add('collapsed');
            });
        })
    }

    initSidebars() {
        const toggle = document.querySelectorAll('.js-sidebar-toggle');
        let sidebarActive = false;
        toggle.forEach(el => {
            el.addEventListener('click', (e) => {
                const toggleEl = e.target.closest('.js-sidebar-toggle');
                if (toggleEl.dataset.sidebarTarget) {
                    const sidebar = document.getElementById(toggleEl.dataset.sidebarTarget)
                    if (sidebarActive) {
                        document.querySelector('html').classList.remove(this.noScrollClass);
                        document.querySelector('body').classList.remove(this.noScrollClass);
                        sidebar.classList.remove('active');
                    } else {
                        document.querySelector('html').classList.add(this.noScrollClass);
                        document.querySelector('body').classList.add(this.noScrollClass);
                        sidebar.classList.add('active');
                    }
                    sidebarActive = !sidebarActive;
                }
                e.stopPropagation();
            });
        })
    }

    initTooltips() {
        const tooltipTriggerList = [].slice.call(document.querySelectorAll('[data-bs-toggle="tooltip"]'))
        tooltipTriggerList.forEach(tooltipTriggerEl => {
            new bootstrap5.Tooltip(tooltipTriggerEl)
        })
    }

    initHeaders() {
        window.LS_COMPARATOR_KEY = "comparator_objects";
        window.SBBreakpoints = {
            LG: 1200,
            MD: 992,
            SM: 768,
        };
        window.SBFetchHeadersFnc = () => {
            return new Headers({
                "X-Requested-With": "XMLHttpRequest",
                //"X-CSRFToken": getCookie('csrftoken')
            })
        }

        window.SBFetchHeaders = window.SBFetchHeadersFnc();

        window.SBJSONFetchHeadersFnc = () => {
            return new Headers({
                "X-Requested-With": "XMLHttpRequest",
                // "X-CSRFToken": getCookie('csrftoken'),
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            });
        }

        window.SBJSONFetchHeaders = window.SBJSONFetchHeadersFnc();

        document.body.addEventListener('htmx:configRequest', (event) => {
            event.detail.headers['X-CSRFToken'] = getCookie('csrftoken');
        });
    }

    resizeSensitiveFnc(fnc, invokeImmediate = true) {
        if (invokeImmediate) {
            fnc();
        }
        window.addEventListener('resize', debounce(fnc, 100));
    }

    initScrollTo() {
        const hash = window.location.hash;
        if (hash && hash.length > 0) {
            const el = document.querySelector(hash)
            if (el) {
                setTimeout(() => {
                    window.scrollTo({top: el.offsetTop, behavior: "smooth"});
                }, 100)
            }
        }

        document.addEventListener('click', event => {
            const linkWithHash = event.target.closest('a[href*="#"]');
            if (linkWithHash) {
                const href = linkWithHash.getAttribute('href');
                if (href && href !== '#' && href !== '#0' &&
                    !linkWithHash.hasAttribute('data-toggle') &&
                    !linkWithHash.hasAttribute('data-bs-toggle')
                ) {
                    const el = document.querySelector(href)
                    if (el) {
                        window.scrollTo({top: el.offsetTop, behavior: "smooth"});
                    }
                }
            }
        });
    }

    initBoostrapModalHistoryApi() {
        const modals = document.querySelectorAll('div.modal')
        modals.forEach((modal) => {
            modal.addEventListener('show.bs.modal', (e) => {
                window.history.pushState('', document.title, `${window.location.pathname}#${e.target.id}`)
                window.onhashchange = () => {
                    if (window.location.hash !== e.target.id) {
                        window.bootstrap5.Modal.getInstance(e.target).hide()
                    }
                }
            })
            modal.addEventListener('hidden.bs.modal', () => {
                if (window.location.hash !== '') {
                    // if location hash is still 'something' modal was not hidden by browser navigation
                    // and we need to go back
                    window.history.replaceState('', document.title, window.location.pathname)
                }
            })
        })
    }

    showLoader(el) {
        el.classList.add('active');
    }

    hideLoader(el) {
        el.classList.remove('active');
    }
}