import GenericSlider from "./generic_slider";

window.SBGenericSlider = GenericSlider;

window.addEventListener("DOMContentLoaded", () => {
    new window.SBGenericSlider('[data-generic-slider]');
});

document.addEventListener('shopify:section:load', function (event) {
    const slider = event.target.classList.contains('slideshow');
    if (!slider) return;
    new window.SBGenericSlider('[data-generic-slider]');
});